import {typeList} from '../../utils/selectLists.js'
export default {
	data() {
		return {
			typeList:typeList,
			infoArr: [
				{
					name: '姓名',
					value: '',
					key:'realname',
				},
				{
					name: 'ESN账号',
					value: '',
					key:'esn',
					
				},
				{
					name: '身份证号',
					value: '',
					key:'sfzjhm',
				},
				{
					name: '类别',
					value: '',
					key:'zysflb',
					special:'typeList'
				}
			]
		};
	},
	mounted() {
		this.getUserInfo();
	},
	methods: {
		findValue(arrName,source){
			if(!this[arrName]) return ''
			let target = ''
			this[arrName].forEach((item,index)=>{
				if(item.id == source){
					target = item.value
				}
			})
			return target
		},
		getUserInfo() {
			this.$HTTP({
				url: '/User/userData',
				params: {
					userId: uni.getStorageSync('userId')
				},
				successCallback: ({data}) => {
					if(data.code == '0'){
						let resData = data.data
						this.infoArr.forEach((item,index)=>{
							if(item.special){
								item.value = this.findValue(item.special,resData[item.key])
							}else{
								item.value = resData[item.key]
							}
						})
					}
				}
			});
		}
	}
};
